import { SET_CLIENTS, SET_CLIENT_ME } from '../actions/clients';

const defaultState = {
	clients: [],
	clientMe: {}
};

export default function clients(state = defaultState, action) {
	switch (action.type) {
		case SET_CLIENTS:
			return Object.assign({}, state, {
				clients: [...action.clients]
			});
		case SET_CLIENT_ME:
			return Object.assign({}, state, {
				clientMe: action.clientMe
			});
		default:
			return state;
	}
}



